/**
 * Darbinieku dzimanas dienu bloka JavaScript funkcionalitte
 * 
 * @type _L4.Anonym$0|Function
 */
var BlockEmplbirth = function()
{ 
    /**
     * Meklanas rku HTML formas elements
     * 
     * @type Object
     */
    var form_elem = null;
    
    /**
     * Darbinieku meklanas bloka HTML elements
     * 
     * @type type
     */
    var page_elem = null;
        
    /**
     * Atgrie elementa vrtbu. Ja elements nav atrasts, tad noklusto vrtbu
     * 
     * @param {Object} parent_elem Veckais elements, kur tiks veikta meklana
     * @param {string} selector    Mekljamais elements
     * @param {mixed} default_val  Noklust vrtba
     * @returns {mixed}
     */
    var getElementVal = function(parent_elem, selector, default_val) {
        var elem = parent_elem.find(selector);

        if (!elem.length)
        {
            return default_val;
        }

        return elem.val();
    };

    /**
     * Parda vai paslpj brdinjumu, ka jnorda uzmums, lai izvltos sturktrvienbu
     * 
     * @param {Object} page_elem Lapas elements
     * @returns {undefined}
     */
    var showHideWarning = function(){
        var btn_choose = page_elem.find(".dx-tree-value-choose-btn");
        
        if (getElementVal(page_elem, 'select[name=source_id]', 0) > 0){
            // noemam brdinjumu, ja bija uzstdts            
            if (btn_choose.hasClass('tooltipstered')) {
                btn_choose.tooltipster('disable');
            }            
        }
        else{             
            if (!btn_choose.hasClass('tooltipstered')) {
                // uzstdam brdinjumu pirmo reizi
                btn_choose.tooltipster({
                    theme: 'tooltipster-light',
                    animation: 'grow',
                    content: Lang.get('employee_fields.unit_hint')
                });
            }
            else
            {
                // iespjojam jau uzstdtu brdinjumu
                btn_choose.tooltipster('enable');
            }
        }
    };

    /**
     * Parda vai paslpj saiti "Notrt kritrijus" atkarba ir vai nav nordts vismaz viens kritrijis
     * 
     * @returns {undefined}
     */
    var clearLinkShowHide = function() {
        if (
                getElementVal(page_elem, 'select[name=source_id]', 0) > 0 ||
                getElementVal(page_elem, 'input[name=department]', "") ||
                getElementVal(page_elem, 'input[name=criteria]', "") ||
                getElementVal(page_elem, 'input[name=date_from]', "")
                )
        {
            page_elem.find(".dx-clear-link").show();
        }
        else
        {
            page_elem.find(".dx-clear-link").hide();
        }
    };

    /**
     * Notra meklanas kritriju lauku vrtbas
     * 
     * @returns {undefined}
     */
    var clearFields = function() {
        form_elem.find('input[name=criteria]').val('');
        form_elem.find('select[name=source_id]').val(0);
        form_elem.find('input[name=department]').val('');
        form_elem.find('#defaultrange input').val('');
        form_elem.find('input[name=date_from]').val('');
        form_elem.find('input[name=date_to]').val('');
        
        // uzstda brddinjumu, ka nav nordts uzmums (nevar izvlties struktrvienbu)
        showHideWarning();
    };

    /**
     * Nodroina struktrvienbu izvlnes loga popup atvranu
     * 
     * @returns {undefined}
     */
    var handleDepartmentChoose = function() {
        page_elem.find(".dx-tree-value-choose-btn").click(function(e) {

            var company_id = page_elem.find("select[name=source_id]").val();

            if (company_id == 0)
            {
                page_elem.find("select[name=source_id]").focus();
                page_elem.find("select[name=source_id]").simulate('mousedown');
                return;
            }

            get_popup_item_by_id_ie9(company_id, 'ajax/departments', page_elem.find("select[name=source_id] option:selected").text() + ' - struktrvienbas izvle', initTree);
        });
    };

    /**
     * Nodroina struktrvienbas lauka notranu
     * 
     * @returns {undefined}
     */
    var handleDepartmentClear = function() {
        page_elem.find(".dx-tree-value-clear-btn").click(function(e) {
            page_elem.find("input[name=department]").val('');
            clearLinkShowHide(page_elem);
        });
    };
   
    /**
     * Nodroina saites "Notrt" funkcionalitti - dz formas lauku vrtbas
     *
     * @returns {undefined}
     */
    var handleLinkClear = function() {
        page_elem.find(".dx-clear-link").click(function() {
            clearFields();
            clearLinkShowHide();
        });
    };

    /**
     * Inicializ vrtbu notranas saites pardanu/paslpanu, mainoties formas lauku vrtbm
     * 
     * @returns {undefined}
     */
    var handleValueChange = function() {
        form_elem.find("input").keyup(function() {
            clearLinkShowHide();
        });

        form_elem.find("select").change(function() {
            clearLinkShowHide();
        });
    };
    
    /**
     * Nodroina brdinajuma uzstdanu/paslpanu, atkarb vai nordts uzmums
     * 
     * @returns {undefined}
     */
    var handleSourceChange = function(){
        form_elem.find("select[name=source_id]").change(function() {
            showHideWarning();
        });       
    };    

    /**
     * Inicializ meklnas rku pardanu pie lapas ieldes - parda, ja bija meklanas pc kda no redzamajiem formas laukiem
     * 
     * @returns {undefined}
     */
    var initSearchTools = function()
    {
        if (
                getElementVal(page_elem, 'select[name=source_id]', 0) > 0 ||
                getElementVal(page_elem, 'input[name=department]', "") ||
                getElementVal(page_elem, 'input[name=date_from]', "")
                )
        {
            SearchTools.showHideTools();
        }
    };
    
    /**
     * Inicializ datuma intervla uzstdanas komponenti
     * 
     * @returns {undefined}
     */
    var initDateRange = function() {
        var arr_date_param = {
            range_id: "defaultrange",
            el_date_from: "date_from",
            el_date_to: "date_to",
            page_selector: ".dx-employees-birth-block",
            form_selector: ".search-tools-form",
            arr_ranges: {}
        };
        
        arr_date_param["arr_ranges"][Lang.get('date_range.flt_today')] = [moment(), moment()];
        arr_date_param["arr_ranges"][Lang.get('date_range.flt_tomorrow')] = [moment().add('days', 1), moment().add('days', 1)];
        arr_date_param["arr_ranges"][Lang.get('date_range.flt_yesterday')] = [moment().subtract('days', 1), moment().subtract('days', 1)];
        arr_date_param["arr_ranges"][Lang.get('date_range.flt_7')] = [moment().subtract('days', 6), moment()];
        arr_date_param["arr_ranges"][Lang.get('date_range.flt_thism')] = [moment().startOf('month'), moment().endOf('month')];
        arr_date_param["arr_ranges"][Lang.get('date_range.flt_nextm')] = [moment().add('month', 1).startOf('month'), moment().add('month', 1).endOf('month')];
        arr_date_param["arr_ranges"][Lang.get('date_range.flt_prevm')] = [moment().subtract('month', 1).startOf('month'), moment().subtract('month', 1).endOf('month')];
        
        DateRange.init(arr_date_param, clearLinkShowHide);
    }
    
    /**
     * Inicializ darbinieku lapas JavaScript funkcionalitti
     * 
     * @returns {undefined}
     */
    var initPage = function() {        
        
        page_elem = $(".dx-employees-birth-block");
        form_elem = page_elem.find('.search-tools-form');
        
        SearchTools.init(clearLinkShowHide, clearFields);
                
        handleDepartmentChoose();
        handleDepartmentClear();

        handleValueChange();
        handleLinkClear();
        handleSourceChange();
        
        EmployeesLinks.setFormUrl('search');
        EmployeesLinks.init($(".dx-employees-birth-page"), clearFields);
        
        initDateRange();

        clearLinkShowHide();
        initSearchTools();
        showHideWarning();       
    };

    /**
     * Inicializ struktrvienbu koku
     * 
     * @returns {undefined}
     */
    var initTree = function() {        
        $(".dx-department-tree-container").on('select_node.jstree', function (e, data) {
            var i, j;
            for(i = 0, j = data.selected.length; i < j; i++) {
                $(".dx-employees-birth-block input[name=department]").val(data.instance.get_node(data.selected[i], true).text());
                $( '#popup_window' ).modal('hide');
                break;
            }
            
        }).jstree({
            "core" : {"multiple" : false },
            
        });
    };

    return {
        init: function() {
            initPage();
        },
        initTree: function() {
            initTree();
        }
    };
}();

$(document).ready(function() {
    BlockEmplbirth.init();
});

$(document).ajaxComplete(function(event, xhr, settings) {
    BlockEmplbirth.initTree();
});